<template>
  <div>
    <NavTemplent title="物料费用"></NavTemplent>
    <div style="padding: 15px 10% 15px 15px;">
      <div>
        <h5 style="font-weight: bold">数据筛选</h5>
        <el-form style="margin-top: 15px" label-width="80px" inline @submit.native.prevent size="mini">
          <el-date-picker
            style="width: 250px"
            size="mini"
            v-model="form.date"
            type="daterange"
            format="yyyy/MM/dd"
            start-placeholder="开始日期"
            end-placeholder="结束日期">
          </el-date-picker>
          <el-form-item label="费用类型">
            <el-select v-model="form.departmentId" placeholder="请选择部门">
              <el-option v-for="item in departmentList" :key="item.department_id" :value="item.department_id" :label="item.department_name"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="站点">
            <el-select v-model="form.site" placeholder="请选择站点">
              <el-option v-for="item in siteList" :key="item.web_id" :value="item.web_id" :label="item.web_name"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary">
              <i class="el-icon-search"></i>
              筛选
            </el-button>
          </el-form-item>
        </el-form>
      </div>
      <el-row class="tableTitle">
        <el-col :span="24">
          <h5>物料费用</h5>
        </el-col>
      </el-row>
      <div>
        <div id="myChart" style="width: 100%;height: 300px"></div>
      </div>
    </div>
  </div>
</template>
<script>
    import NavTemplent from "../../../components/NavTemplent";
    import TablePage from "../../../components/TablePage";
    export default {
        components: {
            NavTemplent,
            TablePage
        },
        data() {
            return {
                siteList: null,
                departmentList: [],
                page: {
                    pageSize: 1,
                    pageNum: 50
                },
                title: '编辑费用',
                form: {
                    date: [],
                    departmentId: '',
                    siteName: ''
                }
            }
        },
        methods: {
            getSiteList() {
                this.$post('/getGaWebSitList', {
                    pageNum: 10000,
                    page: this.page.pageSize,
                    website_name: this.form.siteName
                }).then(res => {
                    this.siteList = res.list
                })
            },
            drawLine() {
                let myChart = this.$echarts.init(document.getElementById('myChart'));
                myChart.setOption({
                    xAxis: {
                        type: 'category',
                        data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [{
                        data: [574, 932, 604, 934, 290, 1130, 720],
                        type: 'line'
                    }]
                });
            },
            getDepartmentList() {
                this.$post('/getDepartmentList').then(res => {
                    this.departmentList = res.list
                }).catch(err => {
                    this.$message.error(err)
                });
            }
        },
        mounted() {
            this.getSiteList();
            this.getDepartmentList();
            this.drawLine()
        }
    }
</script>

<style lang="scss" scoped>
  .tableTitle{
    padding: 15px;
    h5{
      font-weight: bolder;
    }
  }
  .el-select{
    width: 100%;
  }
  .tab {
    margin: 10px;
    .active{
      background: #409EFF;
      color: #fff;
    }
    span {
      font-size: 14px;
      border-radius: 5px;
      border: 1px solid #e6e3e3;
      padding: 5px 10px;
    }
  }
</style>



